const applyForceDirectedLayout = (nodes, edges, setNodes) => {
    // Create a simulation with nodes
    const simulation = forceSimulation(nodes)
      .force('charge', forceManyBody().strength(-200)) // Repulsive force
      .force('center', forceCenter(window.innerWidth / 2, window.innerHeight / 2)) // Centering force
      .force('link', forceLink(edges).id((d) => d.id).distance(100)) // Link force
      .on('tick', console.log("ticked"));
    // Run the simulation for a few iterations
    simulation.tick(300);
  
    // Stop the simulation
    simulation.stop();
  
    // Update node positions
    nodes.forEach((node) => {
      node.position = { x: node.x, y: node.y };
    });
  
    return nodes;
  };



const processData = (raw_data) => { 

    if(!raw_data){return [[], []]} 

    var data = JSON.parse(raw_data)

    if(data.processed){return [data.nodes, data.edges]}

    var initialNodes = data.nodes.map(item => {
        item.type = 'selectorNode'
        item.data = {id: item.id, label: item.id, "Node ID": item.node_id, "": "" }  // all editable item should go to data. All things that should not be shown in node needs to be addressed in node 
        // item.sourcePosition = 'top'
        // item.targetPosition = 'bottom'
        return item;
    })

    var categories = [...new Set(initialNodes.map(i => i.nodeType))]
    var initialStyleBasedOnCategory = {}
    categories.forEach((item, index) => {
        initialStyleBasedOnCategory[item] = colorCombinations[index]
    })

    var initialNodes = data.nodes.map(item => {
        item.style = { 
            border: '3px solid #777', 
            padding: 15, 
            backgroundColor: initialStyleBasedOnCategory[item.nodeType]["bg"],
            color: initialStyleBasedOnCategory[item.nodeType]["fg"],
            borderRadius: '10px 10px 10px 10px '
        }
        return item;
    })
    

    //console.log(initialNodes)

    var initialEdges = data.edges.map(item => {
        // item.type = 'smoothstep'; 
        item.markerEnd = {
            type:'arrowclosed',
            color: "blue",
            // width: 30,
            // height: 30, 
            // storkewidth: 5,
        }
        item.stroke=100
        item.animated = true
        // item.label ="label"
        item.type = 'customSmooth'
        // item.style={
        //     strokeWidth: 3,      // Width of the edge
        //     stroke: 'blue',      // Color of the edge
        //     strokeDasharray: '5', // Dashed line style
        // }
        item.visible = true
        return item
    }) 

// DATA AUGMENTATION

    // remove outfalls
    var outfalls = initialNodes.filter(item => item.outfall).map(i => i.id)
    initialNodes = initialNodes.filter(item => !outfalls.includes(item.id))
    initialEdges = initialEdges.filter(item => (!outfalls.includes(item.source) && !outfalls.includes(item.target)))

    // Removing Isolated nodes to be removed later
    var list_of_connected_nodes = []
    initialEdges.forEach(item => {
        if(!list_of_connected_nodes.includes(item.source)){list_of_connected_nodes.push(item.source)}; 
        if(!list_of_connected_nodes.includes(item.target)){list_of_connected_nodes.push(item.target)}; 
    })
    initialNodes = initialNodes.filter(item => list_of_connected_nodes.includes(item.id))
    initialEdges = initialEdges.filter(item => list_of_connected_nodes.includes(item.source) && list_of_connected_nodes.includes(item.target))

// ==== DATA AUGMENTATION ENDS =======

    // degre implementation
    // initialNodes = alignNodes(initialNodes, initialEdges)

    // d3 - force implementation

    function normalizeCoordinates(nodes) {
        const xs = nodes.map(node => node.x);
        const ys = nodes.map(node => node.y);
        const minX = Math.min(...xs);
        const maxX = Math.max(...xs);
        const minY = Math.min(...ys);
        const maxY = Math.max(...ys);
      
        return nodes.map(node => ({
          ...node,
          x: ((node.x - minX) / (maxX - minX)) * 2000, // 800 is the width of the schematic
          y: ((node.y - minY) / (maxY - minY)) * 3000, // 600 is the height of the schematic
        }));
      }

    initialNodes = initialNodes.map(node => {node.x = node.location.x; node.y = node.location.y; return node } )
    initialNodes = normalizeCoordinates(initialNodes)
    // initialNodes = applyForceDirectedLayout(initialNodes, initialEdges)
    initialNodes = initialNodes.map(node => {return {...node, position: {x: node.x, y: node.y}}})

    initialEdges.map(item => {
        var snappingEdges = getSnappingSides(initialNodes, item)
        item.sourceHandle = snappingEdges.sourceHandle
        item.targetHandle = snappingEdges.targetHandle
        return item
    })
    return [initialNodes, initialEdges]
}

