export default function Schemtics() {

    const [raw_data, setRawData] = useState(null)

    const [initialNodes, initialEdges] = processData(raw_data)

    const [activeNodeID ,setActiveNodeID] = useState(null)

    const [stepEdges, setStepEdges] = useState(false)
    const [hideIsolatedNodes, setHideIsolatedNodes] = useState(false)
    const [hideOutfalls, setHideOutfalls] = useState(false)

    // console.log(initialNodes, initialEdges)

    const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
    const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
    const [activeLegend, setActiveLegend] = useState(null)

    // updating data on input file change
    useEffect(() => {
      setActiveNodeID(null)
    //   setNodes(initialNodes)
    //   setEdges(initialEdges)
      const updatedNodes = applyForceDirectedLayout([...initialNodes], [...edges]);
      setNodes(updatedNodes);
      setEdges(initialEdges)
    }, [raw_data])

    // for changin style of selected node
    useEffect(() => {
        setNodes((nodes) => {
            const updatedItems = nodes.map((item) => {
                item.style.boxShadow = item.id === activeNodeID ?  '0 4px 20px rgba(255, 0, 0, 0.5)' : 'none'
                return item;
              });
              return updatedItems;
        })
    }, [activeNodeID]) 

    // changing edge type
    useEffect(() => {
        setEdges(() => {
            var newEdges = edges.map(edge => {
                edge.type = stepEdges ?  'customStepped' : 'customSmooth'
                return edge
            })
            return newEdges
        })
    }, [stepEdges])

    // now removal of outfalls and isolated node is hard coded - use the below useEffects to make this use preference.  - commented out respective code
    // handlesr for hide isolated nodes
    useEffect(() => {
        var list_of_connected_nodes = []
        edges.forEach(item => {
            if(!list_of_connected_nodes.includes(item.source) && item.visible === true){list_of_connected_nodes.push(item.source)}; 
            if(!list_of_connected_nodes.includes(item.target) && item.visible === true){list_of_connected_nodes.push(item.target)}; 
        })
        
        setNodes(() => {
            return nodes.map(item => {if(!list_of_connected_nodes.includes(item.id)){item.hidden = hideIsolatedNodes}; return item})
        })


    }, [hideIsolatedNodes])

    // Dynamic Snapping on moving
    useEffect(() => {
        var newEdges = edges.map((edge => {
            var handles = getSnappingSides(nodes, edge); 
            edge.sourceHandle = handles.sourceHandle; 
            edge.targetHandle = handles.targetHandle; 
            return edge 
        }))
        console.log(nodes)
        setEdges(newEdges)
    }, [nodes])

    // handler for hide outfalls    
    useEffect(() => {
        setNodes(() => {
            return nodes.map(item => {item.hidden = item.outfall && hideOutfalls; return item})
        })

        var outfallIDs = []
        nodes.forEach(item => {
            if(item.outfall){
                outfallIDs.push(item.id)
            }
        })

        setEdges(() => {
            return edges.map(item => {
                item.visible = !hideOutfalls || !(outfallIDs.includes(item.source) || outfallIDs.includes(item.target)); 
                return item
            })
        })

    }, [hideOutfalls])


    const onLoad = (reactFlowInstance) => {
        reactFlowInstance.fitView();
    };

    const changeData = (id, key, value) => {
        setNodes((nodes) => {
            const updatedItems = nodes.map((item) => {
                if (item.id === id) {
                  let dataObj = {...item.data}
                  dataObj[key] = value
                  return { ...item, data: dataObj };
                }
                return item;
              });
              return updatedItems;
        })
        // console.log(id, key, value)
    }

    return (
        <div>
            <div id="diagram" style={{ width: '100vw', height: '100vh' }}>
                <ReactFlowProvider>
                    <ReactFlow
                        nodes={nodes}
                        edges={edges.filter(item => item.visible)}
                        nodeTypes={nodeTypes}
                        edgeTypes={edgeTypes}
                        onNodesChange={onNodesChange}
                        onEdgesChange={onEdgesChange}
                        // onConnect={onConnect}
                        onNodeClick={(e, node) => {setActiveNodeID(() => node.id); setActiveLegend({legendID: node.nodeType, style: node.style});}}  
                        minZoom={0.2}
                        fitView
                    >
                        <Controls />
                        <MiniMap />
                        <Background variant="dots" gap={12} size={1} />
                        <JsonFileInput setRawData={setRawData} />
                        <LegendPanel 
                            nodes={nodes} 
                            edges={edges}
                            setNodes={setNodes} 
                            activeLegend={activeLegend} 
                            setActiveLegend={setActiveLegend}
                            activeNodeID={activeNodeID}
                            setActiveNodeID={setActiveNodeID}
                            data={activeNodeID ? nodes.find(item => item.id === activeNodeID)["data"] : {}} 
                            changeData={changeData} 
                            stepEdges={stepEdges}
                            setStepEdges={setStepEdges}
                            hideIsolatedNodes={hideIsolatedNodes}
                            setHideIsolatedNodes={setHideIsolatedNodes}
                            hideOutfalls={hideOutfalls}
                            setHideOutfalls={setHideOutfalls}
                            />
                        <DownloadButton nodes={nodes} edges={edges}/>
                    </ReactFlow>
                </ReactFlowProvider>
            </div>
        </div>
    );
}



