import React, {memo} from 'react';
import { getSmoothStepPath } from 'reactflow';
import { getBezierPath } from 'reactflow';

const edgeStyles = {
  normal: { stroke: '#555', strokeWidth: 2, arrowHeadType: 'arrowclosed', arrowHeadColor: '#555' },
  highlighted: { stroke: '#f00', strokeWidth: 3, arrowHeadType: 'arrowclosed', arrowHeadColor: '#f00' },
};

const CustomEdgeSmooth = memo(({ id, sourceX, sourceY, 
                      targetX, targetY, sourcePosition, 
                      targetPosition, 
                      style = {                 
                        strokeWidth: 3,      // Width of the edge
                        stroke: 'blue',      // Color of the edge
                        strokeDasharray: '5', // Dashed line style
                      }, markerEnd }) => {
  const [edgePath, labelX, labelY] = getSmoothStepPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  return (
    <>
      <path id={id} 
          style={style} 
          className="react-flow__edge-path" 
          d={edgePath} 
          markerEnd={markerEnd} />
    </>
  );
});


const CustomEdgeStepped = memo(({ id, sourceX, sourceY, 
                      targetX, targetY, sourcePosition, 
                      targetPosition, style = {
                        strokeWidth: 3,      // Width of the edge
                        stroke: 'blue',      // Color of the edge
                        strokeDasharray: '5', // Dashed line style
                      }, markerEnd }) => {
  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  return (
    <>
      <path id={id} 
          style={style} 
          className="react-flow__edge-path" 
          d={edgePath} 
          markerEnd={markerEnd} />
    </>
  );
});

export {CustomEdgeSmooth, CustomEdgeStepped};