import React, { memo } from 'react';
import { Handle, Position } from 'reactflow';


const NodeType1 =  memo(({ data, isConnectable }) => {


  return (
    <div className="custom-node">
      <Handle type="target" position={Position.Top} id="top" />
      <Handle type="target" position={Position.Bottom} id="bottom" />
      <Handle type="target" position={Position.Left} id="left" />
      <Handle type="target" position={Position.Right} id="right" />

      <div>
          {Object.entries(data).map(([key, value]) => 
              key !== "id" && value !="" && 
              (key !== "" ? 
                key !== "label" ? 
                <div key = {String(key) + String(data["id"])}>             
                    <label><strong>{key}</strong></label> &nbsp;
                    <label className="display-linebreak">{value}</label>    
                </div> 
                :
                <div key = {String(key) + String(data["id"])}>             
                    <label className="display-linebreak" style={{fontSize: 25}}><strong>{value}</strong></label>    
                </div>
              : 
              <label                                                                              // notes field
                style={{ whiteSpace: 'pre-line' }}
                dangerouslySetInnerHTML={{ __html: value.replace(/\n/g, '<br>') }}
              />)
          )}
      </div>
      
      <Handle type="source" position={Position.Top} id="top" />
      <Handle type="source" position={Position.Bottom} id="bottom" />
      <Handle type="source" position={Position.Left} id="left" />
      <Handle type="source" position={Position.Right} id="right" />
    </div>
  );
});

export {NodeType1}